<template>
  <div class="list bg-[#e7b5b5] p-5">
    <div class="flex justify-between items-center">
      <div @click="$router.push('/')">回上一頁</div>
      <label for="show">
        平假名/説明
        <input id="show" type="checkbox"  :checked="showHiragana" @click="showHiragana = !showHiragana">
      </label>
    </div>
    <div v-for="(item, index)  in list" :key="index" class="card bg-white rounded-md p-1 mb-3 ">
      <div class="flex items-start card-title py-2 mb-1 h-full">
        <div class="w-1/2 flex items-start justify-start card-title-left p-1">
          <p>{{ index + 1 }} </p>
          <div class="ml-3">
            <p class="text-xs text-[#545454]" :class="{ 'invisible opacity-0': !showHiragana }">{{item.hiragana}}</p>
            <p class="text-2xl font-bold text-[#545454]">{{item.kanji}}</p>
          </div>
        </div>
        <div class="w-1/2 flex items-center justify-between p-1">
          <p>{{ item.explain }}</p>
          <input type="checkbox"  :checked="item.isdone === 1" @click="isDone(index, item.isdone)">
        </div>
      </div>
      <div class="text-left">例：{{ item.example }}</div>
      <div class="text-left">{{ item.desc }}</div>
    </div>
  </div>
  <div class="model" v-if="isLoading">
    <div class="loader"></div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios'

export default {
  name: 'SuccessView',
  components: {
    // HelloWorld
  },
  data () {
    return {
      list: [],
      isLoading: false,
      showHiragana: false
    }
  },
  created () {
    // this.getList()
    this.list = [
      {
        kanji: '団結',
        hiragana: 'だんけつ',
        explain: '團結',
        example: 'dsv',
        desc: 'dsvdsv',
        isdone: 1
      },
      {
        kanji: 'sdvsdv',
        hiragana: 'sdv',
        explain: 'sdv',
        example: 'svd',
        desc: 'sdvds',
        isdone: 1
      },
      {
        kanji: 'sdvsdv',
        hiragana: 'sdv',
        explain: 'sdv',
        example: 'svd',
        desc: 'sdvds',
        isdone: 1
      }
    ]
  },
  mounted () {
  },
  methods: {
    goback () {
      this.$router.push('/')
    },
    async getList () {
      this.isLoading = true
      try {
        const response = await axios.get('https://script.google.com/macros/s/AKfycbw36boghGfcFqGwuhgySChawTZ1BAVLA8vDe1MfdSaSERMsUMQOfZTTiqc0nnk8SG1M/exec', {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        console.log(response.data)
        this.list = response.data.list
        this.isLoading = false
        console.log(response.list)
      } catch (error) {
        console.error('Error:', error)
      }
    },
    async isDone (index, currentValue) {
      console.log(index, currentValue)
      this.list[index].isdone = currentValue === 1 ? 0 : 1
      try {
        const response = await fetch('https://script.google.com/macros/s/AKfycbw36boghGfcFqGwuhgySChawTZ1BAVLA8vDe1MfdSaSERMsUMQOfZTTiqc0nnk8SG1M/exec', {
          method: 'POST',
          mode: 'no-cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ value: { row: index, isdone: this.list[index].isdone } }
          )
        })
        const result = await response.text()
        console.log('71', result)
      } catch (error) {
        console.error('Error:', error)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.list{
  .card{
    .card-title{
      border-bottom: 1px solid #777777;
      .card-title-left{
        border-right: 1px solid #777777;
      }
    }
  }
}
.model{
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  .loader {
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #f47189; /* Blue */
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 2s linear infinite;
  }

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
}
</style>
