import axios from 'axios'

export default {
  name: 'HomeView',
  components: {
    // HelloWorld
  },
  data () {
    return {
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    async getList () {
      // 有帶inf時取得會員投票的紀錄
      await axios({
        method: 'get',
        url: 'https://boxapi.uupon.com/2024013/BoxList'
      }).then((response) => {
        this.rewardList = response.data.filter(v => v.state === '0')
      })
      axios({
        method: 'get',
        url: `https://boxapi.uupon.com/2024013/BoxList?inf=${this.encryptedBase64}`
      }).then((response) => {
        if (response.data.length > 0) {
          if (response.data[0].kaiin_no !== '' || response.data.length === 0) {
            this.userList = response.data
            for (let i = 0; i < this.rewardList.length; i++) {
              for (let j = 0; j < this.userList.length; j++) {
                if (this.userList[j].id === this.rewardList[i].id) {
                  this.rewardList[i].mycount = this.userList[j].mycount
                }
              }
            }
          } else {
            location.href = 'https://boxapi.uupon.com/2024013/'
          }
        }
        // this.rewardList = response
      })
    }
  }
}
</script>
